.hero{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-between;align-items:center;width:min(var(--page-width),95vw);min-height:min(600px,100vh);padding:var(--section-paddingY) 0}.hero__text{width:max(25vw,400px);max-width:90vw;min-height:400px;display:flex;flex-direction:column;text-align:left}.hero__text h1{font-size:4.5rem;margin:0;font-weight:400;font-family:var(--logo-font);color:var(--text-color);text-shadow:0 0 3px var(--shadow-color);transition:color .3s ease-in-out,text-shadow .3s ease-in-out}.hero__text h2{font-size:2rem;margin:0;font-weight:400;color:var(--text-color);text-shadow:0 0 1px var(--shadow-color);transition:color .3s ease-in-out,text-shadow .3s ease-in-out;font-style:italic}.hero__text ul{padding:0;margin:0;list-style-position:inside}.hero__text ul li{list-style-type:"✓ ";font-size:1.5rem;font-weight:400;color:var(--text-color);text-shadow:0 0 1px var(--shadow-color);transition:color .3s ease-in-out,text-shadow .3s ease-in-out;font-style:italic}.hero__buttons{margin-top:15px}.hero__button{margin-right:10px;display:inline-block}.hero__button:last-child{margin-right:0}.hero__button:hover{transform:translateY(-2px) scale(1.03);transition:transform .2s ease-in-out}.hero__flashcard{margin-left:auto;margin-right:6px}@media(min-width: 768px){.hero{flex-wrap:nowrap}}